


.navbar {
  position: fixed;
  min-height: 70px;
  width: 100%;
  -webkit-box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
  box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
  background: gray;
  color: white;
  z-index: 99;
  white-space: nowrap;
}

.navbar ul {
  margin: 0;
  padding: 0;
}

.navbar li {
  display: inline-block;
  width: 33.33%;
  text-align: center;
  padding: 26px 0;
}
.nav-logo {

}

.nav-instructions {

}

a:-webkit-any-link {
  cursor: pointer;
  pointer-events: auto;
}
.nav-user a{
  text-decoration: none;
  color: white;
  width: 45%;
  height: 50%;
  position: relative;
  top: 0px;
  border-radius: 10px;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

.nav-user a:active {
  top: 3px;
}

.login:active {
  -webkit-box-shadow: 0px 3px 0px #4492b1;
          box-shadow: 0px 3px 0px #4492b1;
}

.signup:active {
  -webkit-box-shadow: 0px 3px 0px 0px #b14452;
          box-shadow: 0px 3px 0px 0px #b14452;

}

.login {
  padding: 8px 14px;
  margin: 15px 0px 15px 40px;
  background: #63d3ff;
  -webkit-box-shadow: 0px 6px 0px 0px #4492b1, 0px 0px 4px 0px #bbb;
          box-shadow: 0px 6px 0px 0px #4492b1, 0px 0px 4px 0px #bbb;

}
.signup {
  padding: 8px;
  margin: 15px 0px 15px 40px;
  background: rgb(248, 85, 85);
  -webkit-box-shadow: 0px 6px 0px 0px #b14452, 0px 0px 4px 0px #bbb;
          box-shadow: 0px 6px 0px 0px #b14452, 0px 0px 4px 0px #bbb;


}


header {
  padding-top: 70px;
}

.header {
  text-align: center;
  background-image: url('./images/header-background.jpg');
  background-size: cover;
  color: white;

}

h1 {
  padding: 10px;
  opacity: .9;
}

h2 {
  padding: 0px 10px 10px 10px;
  opacity: .9;
}

h3 {
  padding: 30px;
  opacity: .9;
}


